import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import logo from '../images/logo.png';

function Header() {
  const changeState = useDispatch();

  // header.
  const loginLogin = useSelector((state) => state.userlogin);
  const loginUsuario = useSelector((state) => state.username);
  const loginHospital = useSelector((state) => state.hospital);
  const loginCti = useSelector((state) => state.cti);

  // função para retornar ao componente 'Login'.
  const clickLogin = () => {
    changeState({ type: 'LOGIN_ON' });
  };

  // função para retornar ao componente 'Lista'.
  const clickVoltar = () => {
    changeState({
      type: 'UPDATE_CTI',
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: loginHospital,
      payloadCti: loginCti,
    });
  };

  // função para acessar o componente 'Password'.
  const clickPassword = () => {
    changeState({
      type: 'PASSWORD_ON',
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadHospital: loginHospital,
      payloadCti: loginCti,
    });
  };

  return (
    <div
      className="fade-in"
      style={{
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        margin: 0,
        padding: 0,
        width: '100%',
        maxHeight: '20vh',
      }}
    >
      <div
        className="secondary"
        style={{
          backgroundColor: '#E1E5F2',
          borderRadius: 5,
          height: 115,
          marginRight: 0,
        }}
      >
        <img
          alt=""
          src={logo}
          style={{
            marginTop: 15,
            marginBottom: 15,
            marginLeft: -5,
            marginRight: -5,
            height: '90%',
          }}
        ></img>
      </div>
      <div
        className="secondary"
        style={{
          backgroundColor: '#E1E5F2',
          width: '100%',
          height: 115,
        }}
      >
        <p className="title1" style={{color: '#1F7A8C'}}>{loginHospital + ' ' + loginCti}</p>
      </div>
      <div
        className="secondary"
        style={{
          backgroundColor: '#E1E5F2',
          marginLeft: 0,
          height: 115,
        }}
      >
        <button
          className="blue-button"
          title="CLIQUE PARA ALTERAR SUA SENHA."
          onClick={() => clickPassword()}
          style={{
            width: '95%',
            marginBottom: 0,
            paddingLeft: 20,
            paddingRight: 20,
          }}
        >
          OLÁ, {loginUsuario}.
        </button>
        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <button
            className="blue-button"
            title="RETORNAR À LISTA DE PACIENTES."
            style={{ width: 100 }}
            onClick={() => clickVoltar()}
          >
            VOLTAR
          </button>
          <button
            className="blue-button"
            title="FAZER LOGOFF."
            onClick={() => clickLogin()}
            style={{ marginLeft: 0, width: 100 }}
          >
            SAIR
          </button>
        </div>
      </div>
    </div>
  );
}
export default Header;
